.wp-block-create-block-content-cards-image {
	.core-values {
        .circle {
            min-width: 75px;
            height: 65px;
            padding-top: 10px;
            border-radius: 50%;
            margin-right: 25px;
            background:var(--accent-primary);
            display:flex;
            align-items:center;
            justify-content:center;
            font-size:2.5rem;
            color: #353535;
            color: var(--accent-secondary);
            box-shadow:var(--box-shadow);
        }
        h3 {
            color: white;
            // font-family: 'proxima_novaregular';
            // font-family: 'pontiacblack';
        }
    }
    .core-values p {
        color: #a4a1a1;
    }
}
